body {
margin: 0;
padding: 0;
font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
}

.quote-box {
max-width: 70%;
background-color: rgba(255,255,255,0.3);
padding: 30px 40px;
border-radius: 20px;
border-left: 5px solid;
border-color: rgb(230, 255, 247);
}
.quote-box:after {
content: '';
margin-top: -30px;
padding-top: 0px;
position: relative;
bottom: -60px;
left: 30px;
border-width: 30px 0 0 30px;
border-style: solid;
border-color: rgba(255,255,255,0.3) transparent;
display: block;
width: 0;
}

.placeholder-box{
  display: flex;
  flex-wrap: wrap;
  gap: 5px 10px;
}

.container {
display: flex;
margin: 0;

max-width: 100%;
}

#appContainer {
padding: 0;
height: 100vh;
width: 100vw;
justify-content: center;
align-items: center;

color: #5d8584;
background-color: #88bdbc;

transition: color 1s, background-color 1s;
}

#btnContainer {
justify-content: space-between;
align-self: flex-end;
}

#new-quote{
  border: none;
  border-radius: 5px;
  padding: 5px 20px;
}
#new-quote:hover{
  box-shadow: inset 0 0 100px 100px rgba(255,255,255, 0.2);
}

.btn{
margin: 0 0 0 0;
}
.btn-info{
color: white;
}
a .btn{
color: white;
text-decoration: none;
}

.quote { /* quote text */
text-align: left;
}

.author { /* author text */
text-align: right;
margin: 0 60px 30px 0;
font-size: 1em !important;
}

.content { /* quote and author */
  font-size: inherit;
}

@media only screen and (max-width: 768px) {
  .quote-box {
    max-width: 80%;
    max-height: 80%;
    padding: 30px 30px;
    }

  .author { /* author text */
    text-align: right;
    margin: 0 0 10px 0;
    font-size: 1em !important;
    }
  .btn{
    margin: 0;
    }
}